	module.exports = {"data":{"markdownRemark":{"html":"<h2>Rules of Significant Figures</h2>\n<p>Significant figures of a number are all of the digits that carry meaning to the final representation of a measurement. They represent how precise the measurement is.</p>\n<p>The rules to find which digits of a number are significant figures:</p>\n<ul>\n<li>All non-zero digits are significant</li>\n<li>All zeros between non-sero digits are significant: 202, 2002, 20002</li>\n<li>Leading zeros aren’t significant</li>\n<li>In a decimal number, all trailing zeros represent precision, and thusly are significant</li>\n<li>In a non-decimal number, if there is a ”.” at the end, all figures to the left are significant (except leading zeros)</li>\n</ul>\n<h4>Examples:</h4>\n<ul>\n<li>0.05400 has [4] sig figs: 5, 4, 0 and 0</li>\n<li>0.0054090 has [5] sig figs: 5, 4, 0, 9, and 0</li>\n<li>100 has [1] sig fig: 1</li>\n<li>100.0 has [3] sig figs: 1, 0, and 0</li>\n<li>More Information:</li>\n</ul>\n<h3>More Information</h3>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Significant_figures\">wikipedia article</a></li>\n<li><a href=\"https://www.khanacademy.org/math/arithmetic-home/arith-review-decimals/arithmetic-significant-figures-tutorial/v/significant-figures\">Khan Academy’s video on sig figs</a></li>\n<li><a href=\"https://www.purplemath.com/modules/rounding2.htm\">Purplemath guide to sig figs</a></li>\n<li><a href=\"https://www.omnicalculator.com/math/sig-fig\">nifty little sig fig calculator</a></li>\n</ul>","fields":{"slug":"/mathematics/rules-of-significant-figures/"},"frontmatter":{"title":"Rules of Significant Figures"}}},"pathContext":{"slug":"/mathematics/rules-of-significant-figures/"}}

